import React, { createContext, useReducer } from "react";

const AuthCtx = createContext();
const initialState = { user: { name: "", id: "", email: "" } };

let reducer = (state, action) => {
  switch (action.type) {
    case "reset":
      return initialState;
    case "userLogin":
      return { ...state, user: action.user };
  }
};

const AuthCtxProvider = props => {
  let [state, dispatch] = useReducer(reducer, initialState);
  let value = { state, dispatch };
  return <AuthCtx.Provider value={value}>{props.children}</AuthCtx.Provider>;
};

const AuthCtxConsumer = AuthCtx.Consumer;

export { AuthCtx, AuthCtxProvider, AuthCtxConsumer };
